export default {
	data() {
		return {
			islogin:false,
		}
	},
	onLoad() {
		console.log("onload")
	},
	onShow() {
		try{
			var loginKey = uni.getStorageSync("loginKey");
			if(loginKey.length !=0){
				this.islogin = true;
			}
			   
		}catch(e){
		   console.log("error"+e);
		}
		
		
	},
	methods: {
		login() {
			uni.navigateTo({
				url: './login',
				success: res => {
					console.log("navitologin")
				},
				fail: () => {},
				complete: () => {}
			});

		},
			
		logOut(){
			// this.islogin = false;
			// uni.removeStorageSync("loginKey");
			var that = this;
			uni.showModal({
			    title: '提示',
			    content: '确定退出',
			    success: function (res) {
			        if (res.confirm) {
						
						// uni.removeStorageaSync("loginKey");
						uni.removeStorage({
							key:"loginKey",
							success() {
									console.log("点击了退出");
								that.islogin = false;
							}
						})
					
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		
	
	}
}
